import {
  ComponentElement,
  Ref,
  createRef,
  ref,
  uniqueID
} from '@websublime/wbs-ui-common';
import { LitElement, html } from 'lit';

import { version } from './version';

const metadata = {
  description: 'Header container component',
  link: 'https://cdn.websublime.dev',
  name: 'ws-header',
  qa: 'ws-ui-header',
  scope: '@websublime/wbs-ui-header',
  version
};

/**
 * Header component element
 */
export class WsHeader extends ComponentElement {
  static shadowRootOptions = {
    ...LitElement.shadowRootOptions,
    delegatesFocus: true
  };

  elRef: Ref<HTMLHeadingElement> = createRef();

  /**
   * Element id
   */
  private identifier = uniqueID();

  get element() {
    return { element: this.elRef, registry: this.registry };
  }

  constructor() {
    super(metadata);

    const classes = this.options.className as Array<string>;

    this.classList.add(...classes);
  }

  render() {
    return html`
      <ws-inspector .el=${this.element} .inspect=${this.inspect}></ws-inspector>
      <header id=${this.identifier} ${ref(this.elRef)}>
        <slot name="slogan"></slot>
        <slot name="menu"></slot>
        <slot></slot>
        <slot name="extra"></slot>
      </header>
    `;
  }
}
