import {
  WebComponentOptions,
  defineWebComponent
} from '@websublime/wbs-ui-common';

import { WsHeader } from './header';

interface WsHeaderOptions extends WebComponentOptions {
  className?: string[];
}

/**
 * @public
 */
const registerComponent = (options: WsHeaderOptions = {}, name = 'ws-header') =>
  defineWebComponent(name, WsHeader, { className: ['ws-header'], ...options });

declare global {
  interface HTMLElementTagNameMap {
    'ws-header': WsHeader;
  }
}

export { registerComponent };

export { WsHeader } from './header';
